import { Node } from 'cc';
import { CrosswordLetterComponent } from '../ui/CrosswordLetterComponent';

export class CrosswordCell {

    public x: number;
    public y: number;
    public letter: string;
    public opened: boolean;
    public node: Node;
    public letterComponent: CrosswordLetterComponent;

    constructor(x: number, y: number, letter: string) {
        this.x = x;
        this.y = y;
        this.opened = false;
        this.letter = letter;
        this.node = null;
        this.letterComponent = null;
    }

    setNode(node: Node) {
        this.node = node;
        this.letterComponent = node.getComponent(CrosswordLetterComponent);
    }

    changeState(opened: boolean) {
        this.opened = opened;
        if (this.letterComponent) this.letterComponent.changeState(opened);
    }
}

